body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #f5f5f5;
}

.container {
  padding: 16px;
}

h1 {
  font-size: 24px;
}

.subject-layout {
  display: flex;
  height: calc(100vh - 40px);
}

.sidebar {
  width: 220px;
  background: #eaeaea;
  padding: 12px;
  overflow-y: auto;
}

.editor {
  flex: 1;
  padding: 16px;
}

textarea {
  width: 100%;
  height: 70vh;
  font-size: 16px;
}

/* Mobile */
@media (max-width: 768px) {
  .subject-layout {
    flex-direction: column;
  }

  .sidebar {
    width: 100%;
  }
}

.home-layout {
  display: flex;
  height: 100vh;
  width: 100%;
}

.main {
  flex: 1;
  padding: 16px;
  overflow-y: auto;
}

.calendar-layout {
  display: flex;
  height: 100%;
}

.calendar-main {
  flex: 2;
  padding: 16px;
}

.calendar-panel {
  flex: 1;
  border-left: 1px solid #ddd;
  padding: 16px;
  background: #fafafa;
}

.form-field {
  display: flex;
  flex-direction: column;
  gap: 4px;
  margin-bottom: 12px;
}

.form-field label {
  font-size: 0.85rem;
  font-weight: 500;
  color: #555;
}

.form-field input {
  padding: 8px 10px;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-size: 0.95rem;
}